        <div class="row">
    <div class="col-md-3 text-center" style="margin-bottom:5%;" v-for="meat in fillterMeatsByName" :key="meat.id">
        <div class="card">
        <img class="card-img-top" :src="meat.url" alt="meat" >
         <div class="card-body">
