    </div>
    </div>
    </div>
        
    </div>
</template>

<script>
import { firestoreCore} from '../configs/firebase'

export default {
    name:'Meats',
  data() {
    return {
      meats:[],
      search:'',
    }
    
  },
  firestore() {
    return{
      meats: firestoreCore.collection("Meat"),
    };
  },
  computed:{
    fillterMeatsByName: function(){
       function compare(a, b) {
      if (a.Instock==true && b.Instock==false)
        return -1;
      if (a.Instock==false && b.Instock==true)
        return 1;
      return 0;
    }
      return this.meats.sort(compare).filter(meat => meat.Name.toLowerCase().match(this.search))
    }
  }
}
</script>

