<template>
  <div class="container-fluid p-0">
        <div class="text-center header" v-scroll-reveal>
          <h1>Vegetable</h1>
        </div>
        <div class="text" v-scroll-reveal>
          <div class="container">
            <img src="../components/picture/b-vegetable1.png" alt="vegetable" style="width:100%;margin-top:10%;margin-bottom:10%;">
          </div>
        </div>
        <div class="container " v-scroll-reveal>
          <div class="row align-items-center">
          <img class="col-md-12 responsive" src="../components/picture/about-vegetable2.jpg" alt="">
          <img class="col-md-12 responsive" src="../components/picture/about-vegetable3.jpg" alt="">
          </div>
        </div>
        <div class="text" v-scroll-reveal>
          <div class="container">
            <h3>
              In addition to self-invested organic farms, family farms are also linking and supporting farmers in many localities to convert.
               move to organic farming to get international organic certification to buy products.
            </h3>
            <img src="../components/picture/b-vegetable2.png" alt="vegetable" style="width:100%;margin-top:10%;margin-bottom:10%;">
          </div>
        </div>
        <div class="container">
          <h1 class="text-center">Check our storage</h1>
          <input type="text" v-model="search" placeholder="Production Name" style="margin-bottom:2%;"/>
        <div class="row" >
    <div class="col-md-3 text-center" style="margin-bottom:5%;" v-for="vegetable in fillterVegetablesByName" :key="vegetable.id">
        <div class="card" style="width:100%;">
        <img class="card-img-top" :src="vegetable.url" alt="fruit" >
         <div class="card-body">
        <h1 class="card-title">{{vegetable.Name}}</h1>
        <h3 class=card-text style="margin-bottom:5%;">Reference price: {{vegetable.Price}}</h3>
        <div v-if="vegetable.Instock">
        <h4 class='instock'>Stocking</h4>
        </div>
        <div v-else>
          <h4 class="empty">Out of stock</h4>
        </div>
        </div>
        </div>
    </div>
    </div>
    </div>
    </div>
</template>

<script>
import { firestoreCore} from '../configs/firebase'

export default {
    name:'Vegetables',
    data() {
    return {
      vegetables:[],
      search:'',
    }
    
  },
  firestore() {
    return{
      vegetables: firestoreCore.collection("Vegetables"),
    };
  },
  computed:{
    fillterVegetablesByName: function(){
        function compare(a, b) {
      if (a.Instock==true && b.Instock==false)
        return -1;
      if (a.Instock==false && b.Instock==true)
        return 1;
      return 0;
    }
      return this.vegetables.sort(compare).filter(vegetable => vegetable.Name.toLowerCase().match(this.search))
    }
  }
}
</script>

<style scoped>
.container>h1{
  margin:10%;font-size:40pt;
}
.header h1{
  display:inline-block;margin-top:15%;color:white;font-size:60pt;font-family:'Courier New', Courier, monospace;
  margin-bottom:10%;
}
.header{
  background: rgba(0, 0, 0, .5) url('../components/picture/about-vegetable.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-blend-mode: darken;
}
h1{
  height: 50px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 25pt;
  margin:4%;
}
h3{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin:4%;
  line-height: 200%;
}
.responsive{
  width: 80%;
  height: auto;
  margin: auto;
  padding-bottom:5%;
  
}
.instock{
  color: green
}
.empty{
  color: red;
}
.card img{
  max-height: 200px;
}

@media screen and (max-width:376px) {
  .header h1{
    font-size: 40pt;
  }
  .container h3{
    font-size: 10pt;
  }
  .container>h1{
    font-size: 20pt;
  }
}

</style>