<template>
  <div class="container-fluid p-0">
        <div class="header text-center" v-scroll-reveal>
          <h1>Fruit</h1>
        </div>
        <div class="text" v-scroll-reveal>
          <div class="container text-center">

            <img src="../components/picture/b-fruit1.png" alt="fruit" style="margin-top:10%;margin-bottom:10%;width:100%;">

            <h1 class="text-center title5">
            The Process
            </h1>
            <img src="../components/picture/b-fruit2.png" alt="fruit" style="margin-top:10%;margin-bottom:10%;width:100%;">
            <img src="../components/picture/b-fruit3.png" alt="fruit" style="margin-top:10%;margin-bottom:10%;width:100%;">
            
          </div>
        <div class="container">
              <h3> 3.Shipping process</h3>
          
            <p>
              To ensure product quality and long storage time in distant markets. Family Farm always transports by refrigerated containers 
              with the right temperature for each item. Thereby, ensuring the quality of goods to consumers.
            </p>
            </div>
        </div>
        
        <div class="container">
          <h1 class="text-center title5" style="margin:10%;">Check our storage</h1>
          <input type="text" v-model="search" placeholder="Production Name" style="margin-bottom:2%;"/>
        <div class="row">
    <div class="col-md-3 text-center" style="margin-bottom:5%;" v-for="fruit in fillterFruitsByName" :key="fruit.id">
        <div class="card" style="width:100%;">
        <img class="card-img-top" :src="fruit.url" alt="fruit" >
         <div class="card-body">
        <h1 class="card-title">{{fruit.Name}}</h1>
        <h3 class=card-text style="margin-bottom:5%;">Reference price: {{fruit.Price}}</h3>
        <div v-if="fruit.Instock">
        <h4 class='instock'>Stocking</h4>
        </div>
        <div v-else>
          <h4 class="empty">Out of stock</h4>
        </div>
        </div>
        </div>
    </div>
      </div>
    </div>
    </div>
</template>

<script>

import { firestoreCore} from '../configs/firebase'

export default {
  name:'Vegetables',
   data() {
    return {
      fruits:[],
      search:'',
    }
    
  },
  firestore() {
    return  {
      fruits: firestoreCore.collection("Fruit"),
    };
  },
  computed:{
    fillterFruitsByName: function(){
      function compare(a, b) {
      if (a.Instock==true && b.Instock==false)
        return -1;
      if (a.Instock==false && b.Instock==true)
        return 1;
      return 0;
    }
      return this.fruits.sort(compare).filter(fruit => fruit.Name.toLowerCase().match(this.search))
    }
  }
  
}
</script>

<style scoped>
.header>h1{
  display:inline-block;margin-top:15%;color:white;font-size:60pt;font-family:'Courier New', Courier, monospace;
  margin-bottom:10%;
}
.title5{
  font-size: 40pt;
}
.header{
  background: rgba(0, 0, 0, .5) url('../components/picture/about-fruit1.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-blend-mode: darken;
}
h1{
  height: 50px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 20pt;
  margin:4%;
  
}
p{
  font-size: 20pt;
  margin:5%;
  line-height: 200%;
}
h4{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin:5%;
}
.responsive{
  width: 80%;
  height: auto;
  margin: auto;
  padding-bottom:5%;
}
.instock{
  color: green
}
.empty{
  color: red;
}
.card img{
  height: 200px;
}

@media screen and (max-width:376px) {
  .header>h1{
    font-size: 40pt;
  }
  .title{
    font-size: 25pt;
  }
  .container>h1{
    font-size: 20pt;
  }
  .container>h3{
    font-size: 15pt;
  }
  .container>p{
    font-size: 10pt;
  }
}

</style>