import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Worker from '../views/Worker.vue'
import Contact from '../views/Contact.vue'
import Fruits from '../views/Fruits.vue'
import Meats from '../views/Meats.vue'
import Vegetables from '../views/Vegetables.vue'
import OtherProduction from '../views/OtherProduction.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component:About
  },
  {
    path: '/worker',
    name: 'worker',
    component:Worker
  },
  {
    path: '/contact',
    name: 'Contact',
    component:Contact
  },
  {
  path:'/fruits',
  name:'fruits',
  component:Fruits
  },
  {
    path:'/meats',
    name:'meats',
    component:Meats
  },
  {
    path:'/vegetables',
    name:'vegetables',
    component:Vegetables
  },
  {
    path:'/otherproduction',
    name:'otherprodcution',
    component:OtherProduction
  },
]

const router = new VueRouter({
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
