<template>
<div class="container-fluid p-0">
    <div class="text-center header">
        <h1>
        Other Production
        </h1>
    </div>
    <div class="container">
        <p>
            In addition to the main products, Family Farm also develops a number of new products to suit today's needs by 
            processing available products such as honey, fruit jam, frozen fruit... 
            It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. 
            It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, 
            and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
            It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. 
            The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, 
            content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as 
            their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. 
            Various versions have evolved over the years, sometimes by accident, sometimes on purpose (injected humour and the like).
        </p>
        <div class="text-center">
            <img src="../components/picture/honey-season.png" alt="honey-season" class="responsive">
        </div>
        <div class="storage">
          <h1 class=text-center>Check our storage</h1>
          <input type="text" v-model="search" placeholder="Production Name" style="margin-bottom:2%;"/>
        <div class="row">
    <div class="col-md-3 text-center" style="margin-bottom:5%;" v-for="other in fillterOtherByName" :key="other.id">
        <div class="card" style="width:100%;height:100%;">
        <img class="card-img-top" :src="other.url" alt="other" >
         <div class="card-body">
        <h1 class="card-title">{{other.Name}}</h1>
        <h3 class=card-text style="margin-bottom:5%;">Reference price: {{other.Price}}</h3>
        <div v-if="other.Instock">
        <h4 class='instock'>Stocking</h4>
        </div>
        <div v-else>
          <h4 class="empty">Out of stock</h4>
        </div>
        </div>
        </div>
    </div>
    </div>
    </div>
    </div>
</div>    
</template>
<script>
import { firestoreCore} from '../configs/firebase'
export default {
    name:'OtherProduction',
    data() {
    return {
      others:[],
      search:''
    };
    },
    firestore() {
    return{
      others: firestoreCore.collection("Other productions"),
    };
  },
  computed:{
    fillterOtherByName: function(){
       function compare(a, b) {
      if (a.Instock==true && b.Instock==false)
        return -1;
      if (a.Instock==false && b.Instock==true)
        return 1;
      return 0;
    }
      return this.others.sort(compare).filter(other => other.Name.toLowerCase().match(this.search))
    }
  }
}
</script>

<style scoped>

.storage>h1{
  margin:10%;font-size:40pt;
}

.header>h1{
  display:inline-block;margin-top:15%;color:white;font-size:60pt;font-family:'Courier New', Courier, monospace;
  margin-bottom:10%;
}

.header{
  background: rgba(0, 0, 0, .5) url('../components/picture/other-production.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-blend-mode: darken;
}
h1{
    height: 35%;
    font-size:20pt;
    font-family: 'Courier New', Courier, monospace;
    margin:4%;
    
}

h3{
 
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin:2%;
}
p{
    margin:4%;
    font-size: 25pt;
    line-height: 200%;
    
}
.responsive{
    width: 80%;
    height: auto;
    margin:4%;
}
.card img{
  height: 200px;
}
.instock{
  color: green
}
.empty{
  color: red;
}

@media screen and (max-width:376px) {
  .header>h1{
    font-size: 30pt;
  }
  .container>p{
    font-size: 10pt;
  }
  .storage>h1{
    font-size: 20pt;
  }
}

</style>