<script>

import { firestoreCore} from '../configs/firebase'

export default {
  name:'Contact',
  methods:{
    addContact: async function(){
      await firestoreCore.collection("Form").add({
        Name: this.name,
        Phone: this.phone,
        Email: this.email,
        Comment: this.comment,
      })
      window.location.reload();
    }
  },
  data() {
    return{
      name:"",
      phone:"",
      email:"",
      comment:"",
    };
  },
}
</script>

